<!--
 * @Author: zs.duan
 * @Date: 2022-09-21 10:21:11
 * @LastEditors: zs.duan
 * @LastEditTime: 2023-03-28 19:54:00
 * @FilePath: \vue2+elui+template\src\components\dzs-table\index.vue
-->
<template>
    <div class="table-wrop">
        <div class="list-wrop">
            <el-table
                v-if="!isRedraw"
                :data="tableData"
                class="table"
                ref="dzsTable"
                v-loading="loading"
                @selection-change="handleSelectionChange"
                :stripe="true"
                v-bind="{...tableOptions}"
            >
                <el-table-column type="selection" width="55" v-if="showCheckbox"></el-table-column>
                <template v-for="(option,index) in headerData">
                    <el-table-column
                        v-if="!option.isSlot"
                        :label="option.lable"
                        :width="option.width"
                        :show-overflow-tooltip="true"
                        :prop="option.key"
                        :class-name="'table-column_' + columnClassName"
                        v-bind="{...option.props}"
                        :key="index"
                    ></el-table-column>
                    <el-table-column
                        v-else
                        :class-name="'table-column_' + columnClassName"
                        :key="index"
                        :label="option.lable"
                        :width="option.width"
                        :show-overflow-tooltip="true"
                        v-bind="{...option.props}"
                    >
                        <template slot-scope="scope">
                            <div>
                                <slot :name="option.key" :row="{ ...scope.row }" />
                            </div>
                        </template>
                    </el-table-column>
                </template>
                <el-table-column v-bind="operationOption" label="">
                    <template slot-scope="scope">
                        <el-button type="text" class="btn" size="small" v-for="(item,index) in operation" :key="index">
                            <span @click="onEdit(scope.row)" v-if="item == 'edit'"></span>
                            <span @click="onDetails(scope.row)" v-if="item == 'detail'"></span>
                            <el-popconfirm title="" @confirm="onDel(scope.row)">
                                <template #reference>
                                    <span v-if="item == 'detele'"></span>
                                </template>
                            </el-popconfirm>
                        </el-button>
                        <slot name="tableBtn" :row="{...scope.row }"></slot>
                    </template>
                </el-table-column>
            </el-table>
            <div class="sider-box">
                <dzs-tabel-sider :list="tableHeader" @change="onSiderChange" @changeHeight="changeHeight"></dzs-tabel-sider>
            </div>
        </div>
        <div class="table-footer" v-if="(total > maxSize) && showPagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :page-sizes="[maxSize, maxSize * 2, maxSize * 4]"
                :background="paginationSet.background"
                :layout="paginationSet.layout"
                :total="total"
            ></el-pagination>
        </div>
    </div>
</template>

<script>
/**
 * @name 
 * @props tableList  isSlot:slot
 * @props operation  add  edit  details  del 
 * @props tableHeader key  lable   key  width  isSlot:slot props:el-table-column
 * @props showCheckbox   false   true 
 * @props maxSize  10
 * @props showPagination  true 
 * @props operationOption 
 * @props tableData 
 * @props total 
 * @props tableOptions ui
 * @props loading   tableOptions element-loading-text="" element-loading-spinner="el-icon-loading"
 *
 * @methods SelectionChange  
 * @methods change / /  status:page   status:size 
 * @methods change    status:update   status:delete   status:edit  status:details 
 * @methods onEdit 
 * @methods onDetails 
 * @methods onDel 
 * @methods setSelection 
 *
 */
import dzsTabelSider from "./sider/dzs-tabel-sider.vue";
export default {
    name: "dzs-table",
    components: {
        dzsTabelSider,
    },
    props: {
        /** */
        operation: {
            type: Array,
            default: () => {
                return [];
            },
        },
        /** */
        tableHeader: {
            type: Array,
            default: () => {
                return [];
            },
        },
        /** */
        showCheckbox: {
            type: Boolean,
            default: () => {
                return false;
            },
        },
        /** */
        maxSize: {
            type: Number,
            default: () => {
                return 10;
            },
        },
        /** */
        total: {
            type: Number,
            default: () => {
                return 0;
            },
        },
        /** */
        showPagination: {
            type: Boolean,
            default: () => {
                return true;
            },
        },
        /***/
        operationOption: {
            type: Object,
            default: () => {
                return {};
            },
        },
        /**  el-table*/
        tableOptions: {
            type: Object,
            default: () => {
                return {};
            },
        },
        /***/
        tableData: {
            type: Array,
            default: () => {
                return [];
            },
        },
        /***/
        loading: {
            type: Boolean,
            default: () => {
                return false;
            },
        },
    },
    data() {
        return {
            paginationSet: {
                background: false,
                layout: "total, sizes, prev, pager, next, jumper",
            },
            isRedraw: false,
            headerData: [],
            columnClassName : ""
        };
    },
    watch: {
        tableHeader: {
            handler(val) {
                this.headerData = JSON.parse(JSON.stringify(val));
            },
            deep: true,
            immediate: true,
        },
    },
    mounted() {},
    methods: {
        /***/
        handleSizeChange(num) {
            this.$emit("change", {
                status: "size",
                data: num,
            });
            this.$emit("size-change", num);
        },
        /***/
        handleCurrentChange(num) {
            this.$emit("change", {
                status: "page",
                data: num,
            });
            this.$emit("current-change", num);
        },
        /***/
        onEdit(item) {
            this.$emit("change", {
                status: "edit",
                title: "",
                data: item,
            });
            this.$emit("on-edit", item);
        },
        /***/
        onDetails(item) {
            this.$emit("change", {
                status: "details",
                title: "",
                data: item,
            });
            this.$emit("on-details", item);
        },
        /***/
        onDel(item) {
            this.$emit("change", {
                status: "delete",
                title: "",
                data: item,
            });
            this.$emit("on-del", item);
        },
        /***/
        handleSelectionChange(val) {
            this.$emit("SelectionChange", val);
        },
        /***/
        setSelection(rows) {
            if (rows) {
                rows.forEach((row) => {
                    this.$refs.dzsTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.dzsTable.clearSelection();
            }
        },
        /***/
        onSiderChange(val) {
            this.isRedraw = true;
            this.headerData = val;
            setTimeout(() => {
                this.isRedraw = false;
            }, 100);
        },
        /***/
        changeHeight(e) {
            this.isRedraw = true;
            this.columnClassName = e;
            setTimeout(() => {
                this.isRedraw = false;
            }, 100);
        },
    },
};
</script>

<style lang="less" scoped>
.table-wrop {
    height: 100%;
    position: relative;

    .list-wrop {
        padding: 0 10px;
        overflow-y: scroll;
        -ms-overflow-style: none;
        /* IE 10+ */
        scrollbar-width: none;

        /* Firefox */
        &::-webkit-scrollbar {
            /* Chrome Safari */
            display: none;
        }
        display: flex;
        :deep(.el-table th, .el-table tr) {
            background-color: var(--ThemeColor01);
            color: #282c33;
            .cell {
                font-weight: normal;
            }
        }

        :deep(.el-table__fixed-right-patch) {
            background-color: #f3f8ff;
        }

        :deep(.el-table) {
            tr {
                .table-column_mini {
                    height: 30px;
                }
                .table-column_middle {
                    height: 50px;
                }
                .table-column_max {
                    height: 70px;
                }
            }
        }
    }
    .table {
        border: 1px solid #ebeef5;
    }
    @media screen and (max-width: 768px) {
        .table {
            height: auto;
        }
    }

    .table-footer {
        text-align: right;
        padding-top: 10px;
    }

    @media screen and (max-width: 768px) {
        .table-footer {
            position: relative;
            bottom: 0;
            right: 0;
            z-index: 1;
            margin-top: 20px;
            padding: 20px;
            text-align: right;
            width: 100%;
            overflow-x: auto;
        }
    }

    .operation {
        cursor: pointer;

        > span {
            padding-right: 10px;
            color: var(--ThemeColor);

            span {
                font-size: 14px;
            }

            &:last-child {
                padding-right: 0;
            }
        }
    }

    .empty {
        text-align: center;
        padding-top: 10px;
    }

    .scope-span {
        color: var(--fontColor);
    }

    :deep(.el-tag--dark) {
        border-color: transparent;
        display: inline-block !important;
        padding: 0 10px;
        margin: 0 auto;
    }
}
.btn {
    span {
        color: var(--ThemeColor);
    }
    :deep(span) {
        color: var(--ThemeColor);
    }
}

@media screen and (max-width: 768px) {
    .table-wrop {
        .btn {
            width: 100%;
            margin-left: 0;
        }
    }
}
</style>
